import { gql, request } from "graphql-request";
gql`
	query all_catalog($key: String!) {
		catalog(key: $key) {
			id
			value
			is_active
			created_at
			updated_at
			parent: parentable {
				... on Menu {
					id
					value
				}
			}
		}
	}
`;
const POKEMONS = gql`
	query pok {
		pokemons(first: 8) {
			name
		}
	}
`;
async function load() {
  const query = POKEMONS;
  const response1 = await request("https://graphql-pokemon2.vercel.app", query);
  if (response1)
    return { response1 };
  throw error(404, "Not found");
}
export {
  load
};
